/* General Styles */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    background-image: url("https://www.google.com/url?sa=i&url=https%3A%2F%2Fpixabay.com%2Fimages%2Fsearch%2Fwhite%2520background%2F&psig=AOvVaw1a8ZeQrUcmoKU3eu8xEAva&ust=1725300201533000&source=images&cd=vfe&opi=89978449&ved=0CBEQjRxqFwoTCNj9_KCqoogDFQAAAAAdAAAAABAE");
    color: #0300a3;
}
.afro-details {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
}

/* Header Styles */
.afro-header {
    position: relative;
    text-align: center;
    color: white;
}

.header-image {
    position: relative;
}

.header-image img {
    width: 100%;
    height: auto;
    display: block;
    filter: brightness(50%);
}

.header-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
    width: 80%;
}

.header-content p {
    font-size: 1.2rem;
    margin: 10px 0;
    animation: fade-in 1.5s ease-in-out forwards;
    animation-delay: 0.3s;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1); /* Adds a subtle shadow for depth */
    letter-spacing: 0.5px;
    transition: color 0.5s ease;
}

@keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateY(30px);
        color: #555;
    }
    60% {
        opacity: 1;
        transform: translateY(-10px);
        color: #666;
    }
    100% {
        opacity: 1;
        transform: translateY(0);
        color: #ffffff;
    }
}

/* Countdown Styles */
.afro-countdown {
    background-color: #009688;
    color: white;
    text-align: center;
    padding: 15px 0;
    font-size: 1.2rem;
    margin-top: -20px;
    border-radius: 8px 8px 0 0;
}

/* Responsive Design */
@media (max-width: 768px) {
    .header-content p {
        font-size: 1rem;
        width: 90%;
    }
}

@media (max-width: 480px) {
    .header-content p {
        font-size: 0.9rem;
        width: 95%;
    }
}
