/* General Styles */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    color: #0300a3;
}

.afro-details {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
}

/* Header Styles */
.afro-header {
    position: relative;
    text-align: center;
    color: white;
}

.header-image {
    position: relative;
}

.header-image img {
    width: 100%;
    height: auto;
    display: block;
    filter: brightness(50%);
}

.header-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    width: 100%;
}

.header-content p {
    font-size: 1.2rem;
    margin: 10px 0;
    animation: fade-in 1.5s ease-in-out forwards;
    animation-delay: 0.3s;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1); /* Adds a subtle shadow for depth */
    letter-spacing: 0.5px;
    transition: color 0.5s ease;
}

@keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateY(30px);
        color: #555;
    }
    60% {
        opacity: 1;
        transform: translateY(-10px);
        color: #666;
    }
    100% {
        opacity: 1;
        transform: translateY(0);
        color: #ffffff;
    }
}

/* Countdown Styles */
.afro-countdown {
    background-color: #009688;
    color: white;
    text-align: center;
    padding: 15px 0;
    font-size: 1.2rem;
    margin-top: -20px;
    border-radius: 8px 8px 0 0;
}

/* Responsive Design */
@media (max-width: 768px) {
    .header-content p {
        font-size: 1rem;
        width: 90%;
    }
}

@media (max-width: 480px) {
    .header-content p {
        font-size: 0.9rem;
        width: 95%;
    }
}

/* Button Styles */
.button-64 {
  align-items: center;
  background-image: linear-gradient(144deg,#20c5bd, #2eb8bd 50%,#00DDEB);
  border: 0;
  border-radius: 8px;
  box-shadow: rgba(9, 170, 143, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  color: #FFFFFF;
  display: flex;
  font-family: Phantomsans, sans-serif;
  font-size: 20px;
  justify-content: center;
  line-height: 1em;
  max-width: 100%;
  min-width: 140px;
  padding: 3px;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  cursor: pointer;
  margin: 10px 0; /* Add some margin for spacing */
}

.button-64:active,
.button-64:hover {
  outline: 0;
}

.button-64 span {
  background-color: rgb(11, 116, 98);
  padding: 16px 24px;
  border-radius: 6px;
  width: 100%;
  height: 100%;
  transition: 300ms;
}

.button-64:hover span {
  background: none;
}

@media (min-width: 768px) {
  .button-64 {
    font-size: 24px;
    min-width: 196px;
  }
}
